$screen-vsm-min: 268px;
$screen-sm-min: 576px;
$screen-md-min: 768px;
$screen-lg-min: 992px;
$screen-xl-min: 1200px;
$screen-xxl-min: 1600px;

@mixin vsm {
    @media (min-width: #{$screen-vsm-min}) {
        @content;
    }
}

@mixin sm {
    @media (min-width: #{$screen-sm-min}) {
        @content;
    }
 }
 @mixin md {
    @media (min-width: #{$screen-md-min}) {
        @content;
    }
 }
 @mixin lg {
    @media (min-width: #{$screen-lg-min}) {
        @content;
    }
 }
 @mixin xl {
    @media (min-width: #{$screen-xl-min}) {
        @content;
    }
 }

 @mixin xxl {
    @media (min-width: #{$screen-xxl-min}) {
        @content;
    }
 }

.text {
    &::selection {
        background-color: #ffca3a;
        color: whitesmoke;
    }
}

.var1 {
    color: transparent;
    font-size: calc(2em + 4vw);
    font-weight: 900;
    -webkit-text-stroke: 1pt whitesmoke;
    @include md {
        -webkit-text-stroke-width: 2pt;
    }
    @include xxl {
        -webkit-text-size-adjust: 3pt;
    }
}

.var2, .var3 { color: #ffca3a }

.var2 {
    font-size: calc(1em + 2vw);
    font-weight: 600;
}

.var3 {
    font-size: calc(.75em + 1vw);
    font-weight: 300;
}